import React, { useEffect } from 'react';
import Helmet from 'react-helmet';
import { connect } from 'react-redux';
import { getContracts, setLoading } from '../../actions/contractActions';
import propTypes from 'prop-types';
import SearchBar from './SearchBar';
import ContractList from './ContractList';

const Main = (props) => {

  useEffect(() => {
    props.setLoading(true);
    props.getContracts();
  }, [props.getContracts]);

  const serveContent = () => {
    if (props.contracts.loading === false) {
      return (
        <div>
          <Helmet>
            <title>Commitments of Traders - tradersCMT</title>
            <meta name="description" content={`Commitments of Traders meta desc goes here.`} />
          </Helmet>
          <main>
            <section>
              <SearchBar />
              <ContractList />
            </section>
          </main>
        </div>
      );
    } else {
      return (
        <main>
          <section>
            <div className="spinner">
              <div className="rect1"></div>
              <div className="rect2"></div>
              <div className="rect3"></div>
              <div className="rect4"></div>
              <div className="rect5"></div>
            </div>
          </section>
        </main>
      );
    }
  }
  return serveContent();
}

Main.propTypes = {
  getContracts: propTypes.func.isRequired,
  contracts: propTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
  contracts: state.contracts
});

export default connect(mapStateToProps, { getContracts, setLoading })(Main);