import React from 'react';
import ChartIcon from './Icons/ChartIcon';
import { Link } from 'react-router-dom';
// import ReactGA from 'react-ga';


const NavBar = () => {


  // ReactGA.initialize('UA-164495098-1');

  return (
    <div>
      <header>
        <Link to='/'><ChartIcon /><h1 className="title">Commitments of Traders</h1></Link>
      </header>
      <nav>The Commitments of Traders is a weekly market report issued by the Commodity Futures Trading Commission (CFTC) enumerating the holdings of participants in various futures markets in the United States.</nav>
    </div>
  );
}

export default NavBar;