import dateFormater from './dateFormatter';
const moment = require('moment');

const getWeekRange = (rawDate) => { //date in format YYYY/MM/DD
  if(rawDate){
    const date = dateFormater(rawDate,'long')
    const startOfWeek = moment(date).startOf('isoWeek').toDate();
    const endOfWeek = moment(date).endOf('isoWeek').toDate();
    const weekRange = moment(startOfWeek).format("MMMM Do YYYY") + ' - ' + moment(endOfWeek).format("MMMM Do YYYY");
    return weekRange;
  } else {
    return 'Loading...'
  }

}

export default getWeekRange;